import redLogo from "../../../../assets/redLogo.png";
export default {
  data() {
    return {
      showPositionValue: false, //提示信息
      toastContent: "",
      redLogo: redLogo,
      isclose: true,
      isTipShow: false,
      invite_phone: "",
      activity_id: "",
      p_uid: "",
      errmsg:""
    };
  },
  mounted() {
  },
  methods: {
    getQueryVariable(variable) {
      var url=window.location.href;
      console.log(url);
      var query =url.split("?")[1]
      var vars = query.split("&");
      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return false;
    },
    getclose() {
      this.isclose = false;
    },
    setTipShow() {
      this.isTipShow = false;
    },
    getRed() {
      var that = this,
        formData;
      if (
        that.invite_phone == "" ||
        !/^1(3|4|5|6|7|8|9)\d{9}$/.test(that.invite_phone)
      ) {
        that.showPositionValue = true;
        that.toastContent = "请您输入正确的手机号码";
      } else {
        formData = {
          // sign: that.$store.state.user.sign,
          // access_token: that.$store.state.user.token,
          p_uid: that.getQueryVariable("p_uid"),
          invite_phone: that.invite_phone,
          activity_id: that.getQueryVariable("activity_id")
        };
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/invite/invite",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 0) {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
              that.$router.push("/redApp");
            } else {
              that.errmsg=response.errmsg;
              that.isTipShow = true;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    getApp(){
      window.location.href ='http://app.hashsl.com/app/index.html';
    }
  }
};



